import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../service/common.service';
import { Color } from 'ng2-charts';
import { Chart } from 'chart.js'

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
})
export class DashboardComponent implements OnInit {
  chartData: any[];
  count: number[];
  datasets: any[];
  graphColor: any = {
    'Cpu': { 'color': ['#337f59', '#39cca4'], 'hover': ['#3a9673', '#41e2b7'] },
    'Memory': { 'color': ['#b7473e', '#f05b4f'], 'hover': ['#b25f59', '#f98f86'] },
    'Storage': { 'color': ['#6d3287', '#cb87d7'], 'hover': ['#986fab', '#e2bce9'] }
  };
  options: any = {
    cutoutPercentage: 75
  }
  /* "#68B6DD","#FED403"*/
  constructor(private CS: CommonService) {
    this.chartData = [];
    this.count = [1, 2, 3, 4, 5];
  }
  ngOnInit() {
    Chart.pluginService.register({
      beforeDraw: function (chart) {
        let percent = ((chart.config.data.datasets["0"].data["0"]) / ((chart.config.data.datasets["0"].data["0"]) + (chart.config.data.datasets["0"].data["1"]))) * 100;
        let percent2 = percent.toFixed(0);
        let width = chart.chart.width,
          height = chart.chart.height,
          ctx = chart.chart.ctx;

        ctx.restore();
        let fontSize = (height / 114).toFixed(2);
        ctx.font = fontSize + "em sans-serif";
        ctx.textBaseline = 'middle';

        let text = percent2 + '%',
          textX = Math.round((width - ctx.measureText(text).width) / 2),
          textY = height / (1.8);

        ctx.fillText(text, textX, textY);
        ctx.save();
      }
    });
    this.CS.getService('/api/v1/requests/inventory').subscribe(
      data => {
        this.drawChart(data.data);
      },
      err => {
        if (err.status == 401) {
          this.CS.showDialog(err);
        } else {
          this.CS.ShowErrorDialog(err);
        }
      },
      () => { }
    );
  }


  drawChart(data) {
    let labels: any[];
    let dataArr: any[];
    for (let props of data) {

      let temp = {};
      labels = [], dataArr = [];
      for (let prop in props) {
        switch (prop) {
          case "name":
            temp["name"] = this.humanize(props[prop]);
            break;
          case "available_qty":
          case "used_qty":
            /* case "total_qty":*/
            labels.push(this.humanize(prop));
            dataArr.push(props[prop]);
            break;
        }
      }
      let colorsOverride: Array<Color> = [{
        backgroundColor: [
          this.graphColor[temp["name"]].color[0],
          this.graphColor[temp["name"]].color[1]
        ],
        hoverBackgroundColor: [
          this.graphColor[temp["name"]].hover[0],
          this.graphColor[temp["name"]].hover[1]
        ]
      }];
      this.datasets = [
        {
          data: dataArr
        }];
      this.Captalize(labels);
      temp["label"] = labels;
      //temp["data"] = dataArr;
      temp["datasets"] = this.datasets;
      temp["chart"] = "doughnut";
      temp["color"] = colorsOverride;
      this.chartData.push(temp);
    }
  }
  // events
  public chartClicked(e: any): void {
    console.log(e);
  }

  public chartHovered(e: any): void {
    console.log(e);
  }
  Captalize(str) {

    //  this.Captalize(prop.replace(/[-!$%^&*()_+|~=`{}\[\]: ";'<>?,.\/]/,' '))
    // let str1=str.split(' ');
    console.log(str);
    // return str.charAt(0).toUpperCase() + str.slice(1);
  };
  humanize(str) {
    const frags = str.split('_');
    for (let i = 0; i < frags.length; i++) {
      frags[i] = frags[i].charAt(0).toUpperCase() + frags[i].slice(1);
    }
    return frags.join(' ');
  }
}



