import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { CommonService } from '../../service/common.service';
//import { LocalDataSource } from 'ng2-smart-table';
import { DomSanitizer } from '@angular/platform-browser';
import { MdDialog } from '@angular/material';
import { PopUpDialogComponent } from '../pop-up-dialog/pop-up-dialog.component';
import { resourceData } from './resources-data';
import { ResourcefilterPipe } from '../../shared/resourcefilter.pipe'

@Component({
  selector: 'app-resources',
  templateUrl: './resources.component.html',
  styleUrls: ['./resources.component.scss'],
  providers: [ResourcefilterPipe]
})
export class ResourcesComponent implements OnInit {
  @ViewChild('myTable') table: any;
  data: any;
  settings: any;
  rows: any = [];
  selected:any = [];
  columns = [
    { prop: 'name' }, 
    { prop: 'operation' },
    { prop: 'description' },
    { prop: 'requestedBy' },
    { prop: 'status' },
    { prop: 'type' }
  ];
  count: number = 0;
  offset: number = 0;
  limit: number = 10;
  //data: LocalDataSource;
  /*invt = {};
  input: string = '<i class="material-icons pointer" (click)="onUserRowSelect()">info</i>';
  settings = {
    columns: {
      type: {
        title: "Type",
        editable: false
      },
      name: {
        title: "Name",
        editable: false
      },
      userId: {
        title: "User Id",
        editable: false
      },
      createdAt: {
        title: "Created At",
        sortDirection: 'desc'
      },
      status: {
        title: "Status",
        editable: false
      },
      Actions: {
        title: 'Info',
        type: 'html',
        valuePrepareFunction: (value) => { return this.DS.bypassSecurityTrustHtml('<i appInfo class="material-icons pointer" >info</i>') },
        filter: false
      }
    },
    actions: {
      add: false,
      edit: false,
      delete: false
    },
    pager: {
      display: true,
      perPage: 10

    }, edit: {
      confirmSave: true
    }
  };
  data:any;*/
  constructor(private CS: CommonService, public dialog: MdDialog, public DS: DomSanitizer, public element: ElementRef, public pipe:ResourcefilterPipe) {
   // this.data = new LocalDataSource();
  }

  getResourceList() {
    this.CS.getService('/api/v1/requests/resources').subscribe(
      data => {
      //  this.data.load(data.data);
      this.data = data.data;
      // Default selection of row
      // this.selected = [this.data[0]];
      this.page(this.offset, this.limit);
      },
      err => {
        if (err.status == 401) {
          this.CS.showDialog(err);
        } else {
          this.CS.ShowErrorDialog(err);
        }

      }
    );
  }

  page(offset, limit) {

    this.count = this.data.length;
    const start = offset * limit;
    const end = start + limit;
    const rows = [...this.rows];

    for (let i = start; i < end; i++) {
      rows[i] = this.data[i];
      let obj = [];
      for (var key in rows[i]) {
        if (rows[i].hasOwnProperty(key)) {
          var element = rows[i][key];
          if(key === 'inventory_items'){
            for (var key in rows[i]['inventory_items']) {
              if(rows[i]['inventory_items'][key].name === 'cpu'){
                let obj1 = {};
                obj1['name'] = this.pipe.transform([rows[i]['inventory_items'][key].name]);
                obj1['qty'] = rows[i]['inventory_items'][key].qty;
                obj.push(obj1);
              }else{
                let obj1 = {};
                obj1['name'] = this.pipe.transform([rows[i]['inventory_items'][key].name]);
                obj1['qty'] = rows[i]['inventory_items'][key].qty + " " + rows[i]['inventory_items'][key].measure;
                obj.push(obj1);
              }
              
            }
          }
          if(key === 'additionalInfo'){
            for (var key1 in rows[i]['additionalInfo']) {
              let obj1 = {};
              obj1['name'] = this.pipe.transform([key1]);
              obj1['qty'] = rows[i]['additionalInfo'][key1];
              obj.push(obj1)
            }
          }
        }
        rows[i]['parameters'] = obj;
      }
    }

    this.rows = rows;

  }

  ngOnInit() {
    this.getResourceList();
  }
  onUserRowSelect(event) {
    let data = event.data;
    this.showPopup(data.inventory_items, data.additionalInfo)
  }
  showPopup(data, additionalInfo) {
    let dialogRef = this.dialog.open(PopUpDialogComponent, {
      data: {
        info: data,
        additionalInfo: additionalInfo
      }
    });
  }

  toggleExpandRow(row) {
    this.table.rowDetail.toggleExpandRow(row);
  }
  onDetailToggle(event) {
    //console.log('Detail Toggled', event);
  }

}



