import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { SocketService } from '../../service/socket.service';
import * as _ from "lodash";
@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements OnInit {
  connection;
  messages;
  notification: any[] = [];
  @Output() notify: EventEmitter<any> = new EventEmitter<any>();
  tempObj: any = { "dataToSend": { "_id": "58fed1649e5f6e1b9835fe0d", "resourceId": "58fed1649e5f6e1b9835fe15", "jobId": "58fed1649e5f6e1b9835fe1f", "name": "sdg", "type": "VM", "operation": "UPDATE", "description": "sdfg", "requestedBy": "admin", "status": "SUCCEEDED", "__v": 0, "parameters": [{ "name": "vmName", "value": "lkjlh", "type": "STRING", "_id": "58fed1649e5f6e1b9835fe14" }, { "name": "resourceId", "value": "58fde7f79e5f6e1b9835fd74", "type": "STRING", "_id": "58fed1649e5f6e1b9835fe13" }, { "name": "vm_Id", "value": "135", "type": "NUMBER", "_id": "58fed1649e5f6e1b9835fe12" }, { "name": "cores", "value": "1", "type": "NUMBER", "_id": "58fed1649e5f6e1b9835fe11" }, { "name": "memory", "value": "514", "type": "NUMBER", "_id": "58fed1649e5f6e1b9835fe10" }, { "name": "storage", "value": "10", "type": "NUMBER", "_id": "58fed1649e5f6e1b9835fe0f" }, { "name": "vm_node", "value": "pve", "type": "STRING", "_id": "58fed1649e5f6e1b9835fe0e" }], "createdOn": "2017-04-25T04:32:36.383Z" }, };
  tt: any[] = [{ operation: "UPDATE", status: "SUCCEEDED", type: "VM", vmName: "lkjlh", vm_Id: "135" }, { operation: "CREATE", status: "FAILED", type: "VM", vmName: "lkjlh", vm_Id: "136" }];
  constructor(private socket: SocketService) {

  }

  ngOnInit() {
    let note = JSON.parse(window.sessionStorage.getItem('notification'));
    let finalObj = {};//_.assign({},{ type: Obj.type, operation: Obj.operation, status: Obj.status },this.findProp(Obj.parameters, ['vmName', 'vm_Id']));  
    if (note != null && note != undefined && note.length > 0) {
      this.notification = note;
      this.notify.emit(this.notification.length);
    }
    this.connection = this.socket.getMessages().subscribe((message: any) => {
      try {
        if (message != null && message != " " && message != undefined) {
          // message = JSON.parse(message); //error on this line while parsing the data send from socket
          let Obj = message.dataToSendNew;
          finalObj = _.assign({}, { type: Obj.type, operation: Obj.operation, status: Obj.status, vm_Id:Obj.vm_Id});
          this.notification.push(finalObj);
          this.notification.reverse()
          this.notify.emit(this.notification.length);
          window.sessionStorage.setItem('notification', JSON.stringify(this.notification));
        }
      } catch (err) {
        console.log(err)
      }
    });
  }
  findProp(Obj, key) {
    try{
    let tempObj = {};
    for (let i = 0; i < key.length; i++) {
      let temp = _.find(Obj, function (o: any) {
        return o.name == key[i];
      })
      tempObj[temp['name']] = temp['value'];
    }
    return tempObj;
  }
catch(err){
  console.log(err);
}
}

}



