import { Component, OnInit, ElementRef, ViewEncapsulation, ViewChild } from '@angular/core';
import { CommonService } from '../../service/common.service';
import { MdDialog } from '@angular/material';
import { PopUpDialogComponent } from '../pop-up-dialog/pop-up-dialog.component';
import { DomSanitizer } from '@angular/platform-browser';
import { requestData } from './request-data';
import { RequestfilterPipe } from '../../shared/requestfilter.pipe';

@Component({
  selector: 'app-request',
  templateUrl: './request.component.html',
  styleUrls: ['./request.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [RequestfilterPipe]
})

export class RequestComponent implements OnInit {
  @ViewChild('myTable') table: any;
  data: any;
  settings: any;
  rows: any = [];
  selected:any = [];
  columns = [
    { prop: 'name' },
    { prop: 'operation' },
    { prop: 'description' },
    { prop: 'requestedBy' },
    { prop: 'status' },
    { prop: 'type' }
  ];
  count: number = 0;
  offset: number = 0;
  limit: number = 10;
  constructor(private CS: CommonService, public dialog: MdDialog, public DS: DomSanitizer, public element: ElementRef, public pipe:RequestfilterPipe) {

  }
  getData() {
    this.CS.getService('/api/v1/request').subscribe(
      data => {
        let DataArray: any;
        if (data.status) {
          this.data = data.data;
          // Default selection of row
          // this.selected = [this.data[0]];
          this.page(this.offset, this.limit);
        }
      },
      err => {
        console.log(err)
        if (err.status == 401) {
          this.CS.showDialog(err);
        } else {
          this.CS.ShowErrorDialog(err);
        }
      },
      () => { });
  }
  page(offset, limit) {

    this.count = this.data.length;
    const start = offset * limit;
    const end = start + limit;
    const rows = [...this.rows];

    //Add Units in storage and Memory
    for (let i = start; i < end - 1; i++) {
      rows[i] = this.data[i];
	  if (rows[i]) {
		  for(var j =0; j<rows[i].parameters.length; j++){
			if (rows[i].parameters[j].name === 'memory'){
				rows[i].parameters[j].value = rows[i].parameters[j].value + ' MB';
			}
			if(rows[i].parameters[j].name === 'storage'){
			  rows[i].parameters[j].value = rows[i].parameters[j].value + ' GB';
			}
		  }
	  }
    }

    this.rows = rows;

  }
  ngOnInit() {
    this.getData();
  }
  onPage(event) {
    this.page(event.offset, event.limit);
  }
  toggleExpandRow(row) {
    this.table.rowDetail.toggleExpandRow(row);
  }
  onDetailToggle(event) {
    //console.log('Detail Toggled', event);
  }
  addNewRequest()
  {
    this.CS.router.navigateByUrl('/home/create-vm');
  }
  getRowHeight(row) { return row.height; }
}





