import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'resourcefilter'
})
export class ResourcefilterPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    switch (value[0]) {
      case 'cpu':{
        value = 'CPU';
        break;
      }
      case 'memory':{
        value = 'Memory';
        break;
      }
      case 'loginPassword':{
        value = 'Login Password';
        break;
      }
      case 'loginUserName':{
        value = 'Login UserName';
        break;
      }
      case 'vmIP':{
        value = 'IP';
        break;
      }
      case 'vmNode':{
        value = 'Node';
        break;
      }
      case 'vmId':{
        value = 'ID';
        break;
      }
      case 'storage':{
        value = 'Storage';
        break;
      }
      default:
        break;
    }
    return value;
  }

}



