import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'requestfilter'
})
export class RequestfilterPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    console.log(value);
    switch (value) {
      case 'cores':{
        value = 'Cores';
        break;
      }
      case 'os':{
        value = 'Operating System';
        break;
      }
      case 'memory':{
        value = 'Memory';
        break;
      }
      case 'vm_name':{
        value = 'VM Name';
        break;
      }
      case 'vmName':{
        value = 'VM Name';
        break;
      }
      case 'resourceId':{
        value = 'Resource ID';
        break;
      }
      case 'vm_Id':{
        value = 'VM ID';
        break;
      }
      case 'vm_node':{
        value = 'VM Node';
        break;
      }
      case 'storage':{
        value = 'Storage';
        break;
      }
      default:
        break;
    }
    return value;
  }

}



